.term_and_condition_container {
  h3 {
    font-family: 'Google Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 10px;
  }

  h2 {
    font-family: 'Google Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    margin-bottom: 10px;
  }

  p {
    margin-bottom: 10px;
    font-family: 'Google Sans';
    font-style: normal;
    font-size: 17px;
  }

  li {
    margin-bottom: 2px;
    font-family: 'Google Sans';
    font-style: normal;
    font-size: 17px;
  }

  .term_and_condition_content {
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .term_and_condition_layout {
    width: 85%;
  }
}
